import React from "react";
import "./NavSidebar.css";
import logoImage from "../../images/logo.png";
import logoutLogo from "../../images/logout-btn.svg";

function NavSidebar() {
	return (
		<nav className="side-navbar-container">
			<div className="side-navbar-body bg-white">
				<header className="side-navbar-head border-bottom d-flex justify-content-center align-items-center flex-column">
					<img src={logoImage} className="side-navbar-head-logo" />
					<button type="button" className="close side-navbar-close-btn">
						<span className="font-weight-light modal-cross-close-btn">
							&times;
						</span>
					</button>
					<h1 className="side-navbar-head-text mt-2">
						<span>TODO</span> <span>Money</span>
					</h1>
				</header>
				<div className="side-navbar-main-content"></div>
				<footer className="side-navbar-footer d-flex justify-content-between align-items-center">
					<h2 className="side-navbar-footer-username ml-3">vaibhav</h2>
					<button className="side-navbar-footer-logout-btn text-white d-flex justify-content-center align-items-center">
						Logout <img src={logoutLogo} className="logout-image ml-2" />
					</button>
				</footer>
			</div>
		</nav>
	);
}

export default NavSidebar;



